import Helper from "@/function.js"
import Md5Unit from '@/pages/utils/md5.js'

export default {
	data() {
		return {
			isShowPass: true,
			isShow: false,
			btnShow: false,
			isChecked: false, //未选中
			isBlue: false, //是否选中
			ifDisplay: false,
			info: {
				userPhone: '',
				userPass: ''
			},

			token: "",
			isApp: true,
			title: "",
			isok:"rgb(175, 175, 175)"
			
		};
	},
	onLoad: function() {

	},
	onShow: function() {











		var token = uni.getStorageSync("token");
		if (!Helper.isEmpty(token)) {
			uni.reLaunch({
				url: "/pages/index/index"
			})
		}

	},
	methods: {
		//https://api.pk535.cn/e/9dvmgdj
		//https://cdridge-download.oss-cn-shanghai.aliyuncs.com/qz.apk
		goApp() {		
			uni.navigateTo({
				url: '/pages/sign/download'
			})
		},
		// 跳转忘记密码页面
		goIndex() {

















			var _this = this;
			var md5Pass = Md5Unit.hex_md5(this.info.userPass);;

			var params = {
				userPhone: this.info.userPhone,
				userPass: md5Pass,
				appId: this.appId
			}
			if(params.userPhone.length<1||params.userPass.length<1){
				uni.showToast({
					title: "请填写账号密码",
					icon: "none"
				})
				return;
			}
			



			uni.showLoading({
				title: "加载中..."
			})

			uni.request({
				url: _this.ApiGateWay + "/sale/Member/Login",
				data: params,
				method: "POST",
				dataType: "json",
				success: function(res) {
					if (Helper.CheckData(res)) {
						var token = res.data.data;
						uni.setStorageSync("token", token);
						uni.reLaunch({
							url: "/pages/index/index"
						})
					} else {
						uni.showToast({
							title: res.data.errorMessage,
							icon: "none"
						})
					}
				},
				complete: function() {
					uni.hideLoading()
				}
			})


		},

		goForetpwd() {
			uni.navigateTo({
				url: '/pages/sign/forgetpwd'
			})
		},
		// 触发input事件
		getInptValue: function(index) {
	
			var _this = this;
			if (_this.info.userPhone.length >= 1 ){
					this.isok="#f02f30";
			}	else{
				this.isok="rgb(175, 175, 175)";
			
			}				

		},

		showPass() {
			this.isShowPass = !this.isShowPass;
		},

		// 合同协议选中
		btnClick: function() {
			var _this = this;
			_this.isChecked = !_this.isChecked;
		},
		//密码显示的否与是
		clearPassword: function() {
			this.info.password = '';
		}
		
		
	}
};
